.signup-background {
  display: flex;
  align-items: center ;
  justify-content: center;
  background-image: url('../public/login_background.jpg')no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: center;
}
  
.sign-box{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: white;
    width: fit-content;
    height: fit-content;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
}

.signup-input-box{
  
  border-top: 0px;
  border-left:0px;
  border-bottom-style: 1px;
  border-right: 0px; 
  border-color: black;
  background-color: aliceblue;
 
}
.signup-text-input{
  display: flex;
  justify-content: flex-start;
}

.signup-box-together{
  display: flex;
  margin-bottom:20px;
  flex-direction: column;
}


.glow-on-hover {
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 7px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: #000
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}